<template>
  <div class="login">
    <div class="container">
      <span style="font-size: 30px;font-weight: bold;color: #5478FE"></span>
      <el-form :model="form" ref="form" class="login-form" :rules="rules">
        <el-form-item label="" prop="phone">
          <el-input v-model="form.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="form.password" type="password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="btn margin-bottom-20" @click="handleLogin"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button class="btn" @click="createAccount"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import {postAction} from "@/api/api";

export default {
  data() {
    return {
      form: {
        phone: '',
        password: '',
      },
      rules: {
        phone: [
          {required: true, message: '', trigger: 'blur'},
          {min: 11, max: 11, message: '', trigger: 'change'}
        ],
        password: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 20, message: '620', trigger: 'blur'}
        ]
      },
    };
  },
  methods: {
    handleLogin() {
      postAction('/user/login', this.form).then(response => {
        if (response.data.status === 'success') {
          this.$message.success('');
        } else {
          this.$message.error(response.data.message);
        }
      })
    },
    createAccount() {
      this.$router.push({path: '/register'})
    }
  }
};
</script>

<style scoped>
.login {
  width: 100%;
  height: 100vh;
  min-height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('/src/assets/login_bg.png');
  background-size: cover;
}

.container {
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 700px;
  margin: auto;
}

.login-form {
  margin: 50px auto;
  width: 50%;
}

.btn {
  width: 150px;
  border-radius: 15px;
  margin-top: 20px;
}
</style>
