<template>
  <div class="register">
    <div class="register-container">
      <span style="font-size: 30px;font-weight: bold;color: #5478FE"></span>
      <el-form :model="form" ref="form" class="login-form" :rules="rules">
        <el-form-item label="" prop="phone">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="form.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rePassword">
          <el-input v-model="form.rePassword" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="btn" @click="handleLogin"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="btn" @click="createAccount"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import {postAction} from "@/api/api";

export default {
  name: 'Register',
  data() {
    return {
      form: {
        phone: '',
        password: '',
        rePassword: '',
      },
      rules: {
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { min: 11, max: 11, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: '620', trigger: 'blur' }
        ],
        rePassword: [
          { required: true, message: '', trigger: ['blur','change'] },
          { validator: this.checkConfirmPassword, trigger: ['blur','change'] }
        ]
      },
    };
  },
  methods: {
    handleLogin() {
      this.$router.push({path: '/'})
    },
    checkConfirmPassword(rule, value, callback) {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.form.rePassword) {
        callback(new Error(''));
      } else {
        callback();
      }
    },
    createAccount() {
      postAction('/user/register', this.form).then(res => {
        if (res.data.status === 'success') {
          this.$message.success('');
          this.$router.push({path: '/'})
        } else {
          this.$message.error(res.data.message);
        }
        this.form = {}
      })
    }
  }
};
</script>

<style scoped>
.register {
  width: 100%;
  height: 100vh;
  min-height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('/src/assets/login_bg_md.png');
  background-size: cover;
}

.register-container {
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 700px;
  margin: auto;
}

.login-form {
  margin: 50px auto;
  width: 50%;
}

.btn {
  width: 150px;
  border-radius: 15px;
  margin-top: 20px;
}
</style>
