<template>
  <div class="system">
    <div class="system-content">
      <div class="title" @click="goto('/agreement')"></div>
      <div class="title"></div>
      <div class="title"></div>
      <div class="title"></div>
      <el-form :inline="true" label-width="100px">
        <el-form-item label="">
          <el-input v-model="downloadPath" placeholder="" style="width: 400px;" readonly />
          <el-button @click="chooseFolder" icon="el-icon-folder-opened"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'system',
  data() {
    return {
      downloadPath: ''
    }
  },
  mounted() {
    this.downloadPath = localStorage.getItem('downloadPath') || 'D:\\Downloads'
  },
  methods: {
    chooseFolder() {
      window.electronAPI.selectFolder().then((path) => {
        if (path) {
          this.downloadPath = path
          localStorage.setItem('downloadPath', path)
        }
      })
    },
    goto(path) {
      this.$router.push(path)
    }
  }
}
</script>

<style scoped>
.system {
  width: 100%;
  height: 100%;
  min-height: 700px;
  padding: 40px;
  box-sizing: border-box;
}

.system-content {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.title {
  width: 400px;
  height: 60px;
  line-height: 60px;
  border-radius: 10px;
  border: 1px solid #BBBBBB;
  text-align: center;
  color: #101010;
  cursor: pointer;
}
</style>